.App {
  text-align: center;
}

.App-logo {
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#logo {
	vertical-align:middle;
}
.navbar-brand span {
	font-size:2rem;
	font-family: Lobster;
	line-height:1.2;
	font-weight: 200;
	display:inline-block;
	vertical-align:middle;
	padding:0 0 0 15px;
	text-shadow:
      -1px 0px 0 #000,
      1px 0px 0 #000,
      -1px 0px 0 #000,
      1px 0px 0 #000;
}

.navbar-brand b {
	display:block;
	font-size:50%;
	line-height:1;
}

.navbar {
	background-color: #811237;
