}

.navbar-nav {
	margin-left:auto;
	background-color: #811237;
}

.nav-item {
	margin-left: 50px;
	margin-right: 40px;
	padding-left: 30px;
	padding-right:30px;
}

.navbtn {
	background-color: #811237;
	color:white;
}

.navbtn:active{
	background-color: wheat;
	color: #811237;
}

